const express = require('express');
const app = express();
const path = require('path');
const bodyParser = require('body-parser');

// Set up express to serve static files from the public folder
app.use(express.static('public'));

// Set up body parser to parse JSON requests
app.use(bodyParser.json());

// Set up routes
app.get('/', (req, res) => {
  res.sendFile(path.join(__dirname, 'index.html'));
});

app.get('/sanctuary', (req, res) => {
  res.sendFile(path.join(__dirname, 'anctuary.html'));
});

app.get('/live-streaming', (req, res) => {
  res.sendFile(path.join(__dirname, 'live-streaming.html'));
});

app.get('/community-forum', (req, res) => {
  res.sendFile(path.join(__dirname, 'community-forum.html'));
});

app.get('/prayer-requests', (req, res) => {
  res.sendFile(path.join(__dirname, 'prayer-requests.html'));
});

app.get('/events', (req, res) => {
  res.sendFile(path.join(__dirname, 'events.html'));
});

app.get('/giving', (req, res) => {
  res.sendFile(path.join(__dirname, 'giving.html'));
});

// Set up route for sending chat messages
app.post('/send-chat-message', (req, res) => {
  // TO DO: implement logic for sending chat message
  console.log('Chat message received:', req.body.message);
  res.send('Chat message sent!');
});

// Start server
const port = 3000;
app.listen(port, () => {
  console.log(`Server started on port ${port}`);
});